.header .header-main__hidden-panel {
  display: none;
  min-height: 70px;
  background: url(../../../img/header-waves.png) bottom center repeat-x, -webkit-gradient(linear, left top, right top, from(#FF65A5), color-stop(100.22%, #FF8076));
  background: url(../../../img/header-waves.png) bottom center repeat-x, -webkit-linear-gradient(left, #FF65A5 0%, #FF8076 100.22%);
  background: url(../../../img/header-waves.png) bottom center repeat-x, -o-linear-gradient(left, #FF65A5 0%, #FF8076 100.22%);
  background: url(../../../img/header-waves.png) bottom center repeat-x, linear-gradient(90deg, #FF65A5 0%, #FF8076 100.22%);
  -webkit-box-shadow: inset 0px 2px 2px rgba(41, 42, 58, 0.25);
          box-shadow: inset 0px 2px 2px rgba(41, 42, 58, 0.25);
}

.header .header-main__hidden-panel_visible {
  display: block;
}

.hidden-panel {
  padding: 10px 158px 15px;
  text-align: right;
}

.hidden-panel__profile {
  display: none;
}

.hidden-panel__profile_visible {
  display: block;
  line-height: 45px;
  padding-right: 100px;
}

.hidden-panel__profile a {
  margin-left: 25px;
  line-height: 30px;
  font-size: 18px;
  text-decoration: none;
  color: #FFFFFF;
}

.hidden-panel__profile a i {
  margin-right: 5px;
}

.hidden-panel__profile a:hover {
  text-decoration: none;
}


