.header .header-main {
  position: relative;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: justify;
      justify-content: space-between;
  min-height: 124px;
  background: url(../../../img/header-waves.png) bottom center repeat-x, -webkit-linear-gradient(356.32deg, #EBE5E9 5.29%, #D1E3E5 95.42%);
  background: url(../../../img/header-waves.png) bottom center repeat-x, -o-linear-gradient(356.32deg, #EBE5E9 5.29%, #D1E3E5 95.42%);
  background: url(../../../img/header-waves.png) bottom center repeat-x, linear-gradient(93.68deg, #EBE5E9 5.29%, #D1E3E5 95.42%);
}

.wrapper {
  height: 100%;
  width: 1205px;
  margin: 0 auto;
}

.h2 {
  text-align: center;
  font-weight: bold;
  font-size: 28px;
  text-transform: uppercase;
  color: #333333;
}





