.header .main-menu {
  min-height: 58px;
  height: 1px;
}

.header .main-menu__items {
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  padding: 0 24px;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-align: center;
      align-items: center;
}

.header .main-menu__item {
  padding-bottom: 10px;
  list-style: none;
  text-align: center;
}

.header .main-menu__item a {
  text-decoration: none;
  text-transform: uppercase;
  font-size: 18px;
  font-weight: bold;
  color: #A1B3BF;
  -webkit-transition: color 300ms ease;
  -o-transition: color 300ms ease;
  transition: color 300ms ease;
}

.header .main-menu__item a:hover {
  color: #333333;
}

.header .main-menu__item_active {
  background-image: -webkit-gradient(linear, left top, right top, from(#FF65A5), to(#FF8076));
  background-image: -webkit-linear-gradient(left, #FF65A5 0%, #FF8076 100%);
  background-image: -o-linear-gradient(left, #FF65A5 0%, #FF8076 100%);
  background-image: linear-gradient(90deg, #FF65A5 0%, #FF8076 100%);
  background-repeat: no-repeat;
  background-position: center bottom;
  background-size: 100% 4px;
}

.header .main-menu__item_active a {
  font-weight: bold;
  color: #333333;
}


