.dropped-menu {
  display: none;
  height: 265px;
  margin-bottom: 24px;
  padding: 32px 0 60px;
  background: #EEEEEE;
  font-family: 'Roboto Condensed', sans-serif;
  border-radius: 10px;
}

.dropped-menu_visible {
  display: block;
}

.dropped-menu .wrapper {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  max-width: 1120px;
}

.dropped-menu__lists {
  -ms-flex: 1 1;
      flex: 1 1;
  height: 100%;
  color: #333333;
}

.dropped-menu__lists_three-coloumns {
  -ms-flex: 4 1;
      flex: 4 1;
}

.dropped-menu__list-title {
  width: 100%;
  margin-bottom: 10px;
  font-weight: 900;
  font-size: 14px;
  text-transform: uppercase;
  color: #333333;
}

.dropped-menu__list {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: start;
      justify-content: flex-start;
  height: 100%;
  list-style: none;
}

.dropped-menu__item {
  margin-right: 28px;
  line-height: 1.5;
}

.dropped-menu__item a {
  font-size: 16px;
  color: #333333;
  text-decoration: none;
  -webkit-transition: color 300ms ease;
  -o-transition: color 300ms ease;
  transition: color 300ms ease;
}

.dropped-menu__item a:hover {
  color: #FF8076;
  text-decoration: none;
}


