.product-catalogue__sort-by {
  text-align: right;
  padding-right: 13px;
}

p.sort-by {
  display: inline-block;
}

.sort-by {
  font-family: 'Roboto', sans-serif;
  font-weight: 300;
  font-size: 16px;
  text-align: left;
  padding-right: 6px;
}

#sorting {
  outline: none;
  width: 220px;
  height: 21px;
  border-radius: 5px;
  padding-left: 9px;
  font-size: 16px;
  font-family: Roboto;
  font-weight: 400;
  line-height: normal;
  text-align: center;
  background: url(../../../img/catalogue-pics/product-catalogue__sort-by-drop-down-chevron.png) no-repeat;
  /* background-color: #A1B3BF; */
  background-position: 195px center;
  -webkit-appearance: none;
  -moz-appearance: none;
  text-indent: 0.01px; 
  -o-text-overflow: ''; 
     text-overflow: '';
  -ms-appearance: none;
  appearance: none;
  color: #848484;
}






